<template>
    <keep-alive>
        <div class="plateMake">
            <div class="plateMake-container">
                您当前选择号码：<span class="plateMake-color">{{ getPlate }}</span>
            </div>
            <div class="plateMake-button">
                <el-button type="danger" @click="$router.go(-1)">返回重选</el-button>
                <el-button type="primary" @click="upDatePlateMake">确认号牌</el-button>
            </div>
        </div>
    </keep-alive>    
</template>
<script>
import { getOpFlow } from '@/api/user'
export default {
    name: 'PlateMake',
    data() {
        return {

        }
    },
    mounted() {
        // console.log(this.$route.params.plate)
    },
    computed: {
        getPlate() {
            return sessionStorage.getItem('plate') ? sessionStorage.getItem('plate') : '';
        }
    },
    methods: {
        upDatePlateMake() {
            const { id } = JSON.parse(sessionStorage.getItem('plateData'))
            getOpFlow({id: id, opStatus: 1, selectedLicensePlate: this.getPlate}).then(resp => {
                console.log(resp)
                this.$router.push({path: '/schedule'})
            })
        }
    },
    
}
</script>
<style lang="scss" scoped>
.plateMake {
    .plateMake-button {
        margin: 30px 0;
        .el-button {
            margin: 0 20px;
        }
    }
    .plateMake-container {
        width: 70%;
        margin: 0 auto;
        height: 200px;
        line-height: 200px;
        text-align: center;
        margin-top: 45px;
        background-color: #3d8ad2;
        color: #fff;
    }
}
</style>